import { useEffect } from "react";
import { Tags } from "../list/Tags";
import styles from "../../styles/CatBtn.module.css";
import Link from "next/link";
import Image from "next/image";

export default function TagBtn() {

  function myCatLoop() {
    var myArray = [];
    var i;
  
    for (i = 0; i < Tags.length; i++) {
      myArray[i] = (
        <CatGrid
          key={i}
          value={i}
          urlname={Tags[i].path[0]}
          thumb={Tags[i].thumb}
          name={Tags[i].Tag}
        />
      );
    }
    return myArray;
  }

  return (
    // <div className={styles.cat_grid_container}>{myCatLoop()}</div>
    <div className={styles.TagList}> {myCatLoop()}</div>
   
  );
}

